
.user {
  margin: 10px;
  max-width: 700px;

  background-color: white;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  text-align: left;
  cursor: pointer;

  transition: box-shadow 0.5s, transform 0.5s;
}

.user:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  transform: scale(1.01);
}

.user__block {
  background-color: black;
  color: #ffffff;
  padding: 20px;
}

.user-info {
  margin: 0;
  padding: 0;
  padding-bottom: 10px;
}

.user-info__header {
  padding-bottom: 20px;
}

.user-info__additional {
  position: relative;
  color: #b3b3b3;
  padding-left: 20px;
}

.user-info__email::before {
  content: '';
  position: absolute;

  left: 0;

  width: 15px;
  height: 15px;

  background: url(../images/mail.svg) center no-repeat;
  background-size: contain;
}

.user-info__email::before {
  content: '';
  position: absolute;

  left: 0;

  width: 15px;
  height: 15px;

  background: url(../images/mail.svg) center no-repeat;
  background-size: contain;
}

.user-info__phone::before {
  content: '';
  position: absolute;

  left: 0;

  width: 15px;
  height: 15px;

  background: url(../images/phone.svg) center no-repeat;
  background-size: contain;
}

.todos {
  padding: 20px;
}

.todos__list {
  list-style: none;
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
}

.todos__list li  {
  margin: 0;
  padding: 5px 0;
}

.todos__list li:hover  {
  background-color: #f3f3f3;
}

.todos__list li:not(:last-child) {
  border-bottom: 1px solid #f0f0f0;
}

.todos__button-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
