.button {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;

  background-color: rgb(25, 0, 168);
  padding: 15px 25px;
  border: none;

  color: #fff;
  text-transform: uppercase;
}

.loader {
  widows: 300px;
  height: 300px;
  background-image: url(../images/loadingCube.gif);
  background-repeat: no-repeat;
  background-position: center;
}

.button--link {
  background: none;
  border: none;
  color: #9e9e9e;
  cursor: pointer;
}

.button--link:hover {
  color: #020202;
}

.button--light-link:hover {
  color: #fff;
}

.todos--header {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 15px;
}

.todos--header h2 {
  margin: 0;
  padding: 0;
}
